<template>
    <div>
        <el-select :value="catId" @input="setCatId"  style="float:left;" v-loading="catLoad" size="mini" placeholder="请选择分组">
            <el-option v-for="(item,index) in local" :key="index" :label="item.name" :value="item.id"></el-option>
        </el-select>
    </div>
</template>

<script>
    import * as api from "@/api";
    export default {
        name: "ResourceSelect",
        props:['catId'],
        data(){
            return{
                catLoad:false,
                local:[],
            }
        },
        created(){
            this.apiGetCategory();
        },
        methods: {
            setCatId(val){
                this.$emit("input",val);
            },
            apiGetCategory(id) { //从数据库中取得分类
                this.catLoad = true;
                let local = [{name: '全部图片', id: 0}];
                api.post('shop/admin.resource.category/index', {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    for (var a in res.list) {
                        local.push({
                            name: res.list[a].title,
                            id: res.list[a].category_id,
                        });
                    }
                    this.local = local;
                    this.catLoad = false;
                }).catch(() => {
                    this.catLoad = false;
                });
            },
        },
    }
</script>

